/* eslint-disable no-unused-vars */
import * as React from 'react'
import { styled } from '@mui/material/styles'
import Radio from '@mui/material/Radio'
import RadioGroup from '@mui/material/RadioGroup'
import FormControlLabel from '@mui/material/FormControlLabel'
import FormControl from '@mui/material/FormControl'
import useStyles from './CustomRadioGroup.style'
import { Typography } from '@mui/material'

const BpIcon = styled('span')(() => ({
  borderRadius: '50%',
  width: 15,
  height: 15,
  backgroundColor: '#fff',
  outline: '1px solid #1d6bf3',
  'input:hover ~ &': {
    backgroundColor: '#ebf1f5',
  },
}))

const BpCheckedIcon = styled(BpIcon)({
  '&:before': {
    display: 'block',
    width: 15,
    height: 15,
    backgroundImage: 'radial-gradient(#1d6bf3,#1d6bf3 41%,transparent 45%)',
    content: '""',
  },
})

function BpRadio(props) {
  return (
    <Radio
      sx={{
        '&:hover': {
          bgcolor: 'transparent',
        },
      }}
      disableRipple
      color="default"
      checkedIcon={<BpCheckedIcon />}
      icon={<BpIcon />}
      {...props}
    />
  )
}

export default function CustomRadioGroup({ field, options, labelTitle, isRequired }) {
  const classes = useStyles()

  return (
    <div className={classes.fieldContainer}>
      {labelTitle && (
        <Typography variant="body2">
          {labelTitle}
          {isRequired && <span style={{ color: '#ff0200' }}> *</span>}
        </Typography>
      )}
      <FormControl>
        <RadioGroup {...field}>
          {options.map((option) => (
            <div key={option?.value} style={{ position: 'relative' }}>
              <FormControlLabel
                value={option?.value}
                control={<BpRadio />}
                label={option?.label}
                className={classes.label}
              />
            </div>
          ))}
        </RadioGroup>
      </FormControl>
    </div>
  )
}
