import React, { useEffect, useState } from 'react'
import { Box, Button, FormHelperText, Typography } from '@mui/material'
import { Controller } from 'react-hook-form'
import useStyles from './PartnersForm.styles'
import CheckboxItem from '../../../Sub/CheckboxGroup/CheckboxItem/CheckboxItem'
import { ControlledTextField } from '../../../Sub/FormElements'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { setCurrentTab, setFormData } from '../../../../services/slices/partners-form-slice'
import ControlledSelectField from '../../../Sub/FormElements/controlled-select-field/controlled-select-field'

function TabPF2({ hookFormMethods }) {
  const dispatch = useAppDispatch()
  const classes = useStyles()
  const { control, handleSubmit, setValue, watch, trigger, errors, getValues } = hookFormMethods
  const [salary, setSalary] = useState(undefined)
  const [salaryMax, setSalaryMax] = useState(undefined)
  const isSalaryNotRequiredChecked = watch('isSalaryNotRequired')
  const { formData } = useAppSelector((state) => state.partnersForm)
  const isFrontend = watch('isFrontend')
  const isPython = watch('isPython')
  const isCplus = watch('isCplus')
  const isGo = watch('isGo')
  const isDevops = watch('isDevops')
  const isTesting = watch('isTesting')
  const isUXDesign = watch('isUXDesign')
  const isGraphicDesign = watch('isGraphicDesign')
  const isAnalitics = watch('isAnalitics')
  const isDataScience = watch('isDataScience')
  const isMarketing = watch('isMarketing')
  const isProjectManagement = watch('isProjectManagement')
  const isHR = watch('isHR')
  const isJava = watch('isJava')

  useEffect(() => {
    const courses = []
    if (isFrontend) courses.push(...[3, 19, 20])
    if (isPython) courses.push(...[1, 18])
    if (isCplus) courses.push(...[5])
    if (isGo) courses.push(...[4])
    if (isDevops) courses.push(...[6])
    if (isTesting) courses.push(...[21, 22, 31])
    if (isUXDesign) courses.push(...[25, 26])
    if (isGraphicDesign) courses.push(...[23, 24])
    if (isAnalitics) courses.push(...[13, 14])
    if (isDataScience) courses.push(...[11, 12])
    if (isMarketing) courses.push(...[7, 8, 9, 10, 16, 30])
    if (isProjectManagement) courses.push(...[17])
    if (isHR) courses.push(...[29])
    if (isJava) courses.push(...[2])
    if (
      isFrontend ||
      isPython ||
      isCplus ||
      isGo ||
      isDevops ||
      isTesting ||
      isUXDesign ||
      isGraphicDesign ||
      isAnalitics ||
      isDataScience ||
      isMarketing ||
      isProjectManagement ||
      isHR ||
      isJava
    )
      setValue('courses', courses, { shouldValidate: true })
  }, [
    isFrontend,
    isPython,
    isCplus,
    isGo,
    isDevops,
    isTesting,
    isUXDesign,
    isGraphicDesign,
    isAnalitics,
    isDataScience,
    isMarketing,
    isProjectManagement,
    isHR,
    isJava,
  ])

  const handleSalaryChange = async (e) => {
    setSalary(e.target.value)
    setValue('salary', e.target.value, { shouldValidate: true, shouldDirty: true })
  }

  const handleSalaryMaxChange = async (e) => {
    setSalaryMax(e.target.value)
    setValue('salary_max', e.target.value, { shouldValidate: true, shouldDirty: true })
    await trigger('salary_max')
    await trigger('salary')
  }

  useEffect(() => {
    if (isSalaryNotRequiredChecked) {
      setSalary('')
      setSalaryMax('')
      setValue('salary', undefined, { shouldValidate: true, shouldDirty: true })
      setValue('salary_max', undefined, { shouldValidate: true, shouldDirty: true })
    }
  }, [isSalaryNotRequiredChecked])

  const onSubmit = (data) => {
    dispatch(setFormData({ ...formData, ...data }))
    dispatch(setCurrentTab(3))
  }

  const handlePreviousClick = () => {
    const values = getValues()
    dispatch(setFormData({ ...formData, ...values }))
    dispatch(setCurrentTab(1))
  }

  return (
    <form className={classes.form} onSubmit={handleSubmit(onSubmit)}>
      <Box className={classes.scrolledArea}>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: '14px', height: 'inherit' }}>
          <Box sx={{ maxWidth: 476, display: 'flex', flexDirection: 'column', gap: '14px' }}>
            <Typography variant="body2">
              В каком направлении вы предлагаете вакансию?
              <span style={{ color: '#ff0200' }}> *</span>
            </Typography>
            <Controller
              name="isFrontend"
              control={control}
              render={({ field }) => (
                <CheckboxItem
                  {...field}
                  checkboxLabel="Фронтенд/ веб-разработка (JS, React, NodeJS)"
                  checked={isFrontend}
                  margin={{ marginBottom: 0 }}
                />
              )}
            />
            <Controller
              name="isPython"
              control={control}
              render={({ field }) => (
                <CheckboxItem
                  {...field}
                  checkboxLabel="Python разработка"
                  checked={isPython}
                  margin={{ marginBottom: 0 }}
                />
              )}
            />
            <Controller
              name="isCplus"
              control={control}
              render={({ field }) => (
                <CheckboxItem
                  {...field}
                  checkboxLabel="С++ разработка"
                  checked={isCplus}
                  margin={{ marginBottom: 0 }}
                />
              )}
            />
            <Controller
              name="isGo"
              control={control}
              render={({ field }) => (
                <CheckboxItem {...field} checkboxLabel="GO разработка" checked={isGo} margin={{ marginBottom: 0 }} />
              )}
            />
            <Controller
              name="isJava"
              control={control}
              render={({ field }) => (
                <CheckboxItem
                  {...field}
                  checkboxLabel="Java разработка"
                  checked={isJava}
                  margin={{ marginBottom: 0 }}
                />
              )}
            />
            <Controller
              name="isDevops"
              control={control}
              render={({ field }) => (
                <CheckboxItem {...field} checkboxLabel="DevOps" checked={isDevops} margin={{ marginBottom: 0 }} />
              )}
            />
            <Controller
              name="isTesting"
              control={control}
              render={({ field }) => (
                <CheckboxItem
                  {...field}
                  checkboxLabel="Тестирование"
                  checked={isTesting}
                  margin={{ marginBottom: 0 }}
                />
              )}
            />
            <Controller
              name="isUXDesign"
              control={control}
              render={({ field }) => (
                <CheckboxItem
                  {...field}
                  checkboxLabel="UX/UI Дизайн"
                  checked={isUXDesign}
                  margin={{ marginBottom: 0 }}
                />
              )}
            />
            <Controller
              name="isGraphicDesign"
              control={control}
              render={({ field }) => (
                <CheckboxItem
                  {...field}
                  checkboxLabel="Графический дизайн (полиграфия, брендинг и др.)"
                  checked={isGraphicDesign}
                  margin={{ marginBottom: 0 }}
                />
              )}
            />
            <Controller
              name="isAnalitics"
              control={control}
              render={({ field }) => (
                <CheckboxItem {...field} checkboxLabel="Аналитика" checked={isAnalitics} margin={{ marginBottom: 0 }} />
              )}
            />
            <Controller
              name="isDataScience"
              control={control}
              render={({ field }) => (
                <CheckboxItem
                  {...field}
                  checkboxLabel="Data Science"
                  checked={isDataScience}
                  margin={{ marginBottom: 0 }}
                />
              )}
            />
            <Controller
              name="isMarketing"
              control={control}
              render={({ field }) => (
                <CheckboxItem
                  {...field}
                  checkboxLabel="Интернет-маркетинг"
                  checked={isMarketing}
                  margin={{ marginBottom: 0 }}
                />
              )}
            />
            <Controller
              name="isProjectManagement"
              control={control}
              render={({ field }) => (
                <CheckboxItem
                  {...field}
                  checkboxLabel="Project Management"
                  checked={isProjectManagement}
                  margin={{ marginBottom: 0 }}
                />
              )}
            />
            <Controller
              name="isHR"
              control={control}
              render={({ field }) => (
                <CheckboxItem {...field} checkboxLabel="IT-рекрутинг" checked={isHR} margin={{ marginBottom: 0 }} />
              )}
            />
            <FormHelperText error className={classes.formHelper}>
              {errors.courses?.message || ' '}
            </FormHelperText>
          </Box>
          <ControlledTextField
            labelTitle="На какую должность вы рассматриваете кандидатов?"
            fieldName="position"
            control={control}
            error={!!errors.position?.message}
            helperText={errors.position?.message}
            placeholder="Введите ответ"
            maxWidth={476}
            isRequired
          />
          <ControlledTextField
            labelTitle="Есть ли дедлайн по получению откликов на эту вакансию?"
            fieldName="date_deadline"
            control={control}
            error={!!errors.date_deadline?.message}
            helperText={errors.date_deadline?.message}
            placeholder="Укажите дату"
            maxWidth={476}
          />
          <Controller
            name="candidate"
            control={control}
            render={({ field: { name, value, onChange } }) => (
              <ControlledSelectField
                labelTitle="Есть ли сейчас кандидат на финальном этапе рассмотрения?"
                tipContent="Выберите из списка"
                name={name}
                value={value || []}
                onChange={onChange}
                error={!!errors?.candidate?.message}
                helperText={errors?.candidate?.message}
                items={[
                  { id: 'Да', name: 'Да' },
                  { id: 'Нет', name: 'Нет' },
                ]}
                displayEmpty
                maxWidth={476}
                isRequired
              />
            )}
          />
          <div className={classes.salaryContainer}>
            <Typography className={classes.salaryLabel} variant="body2">
              Какая з/п вилка для этой вакансии?
            </Typography>
            <div className={classes.salaryInputContainer}>
              <ControlledTextField
                fieldName="salary"
                control={control}
                value={salary}
                error={!!errors.salary?.message}
                helperText={errors.salary?.message}
                placeholder="От"
                onChange={handleSalaryChange}
                maxWidth={148}
                disabled={isSalaryNotRequiredChecked}
              />
              <ControlledTextField
                fieldName="salary_max"
                control={control}
                value={salaryMax}
                error={!!errors.salary_max?.message}
                helperText={errors.salary_max?.message}
                placeholder="До"
                onChange={handleSalaryMaxChange}
                maxWidth={148}
                disabled={isSalaryNotRequiredChecked}
              />
              <Box sx={{ alignSelf: 'baseline', marginLeft: 'auto', marginTop: '7px' }}>
                <Controller
                  name="isSalaryNotRequired"
                  control={control}
                  render={({ field }) => (
                    <CheckboxItem
                      {...field}
                      checkboxLabel="Не указывать"
                      checked={isSalaryNotRequiredChecked}
                      margin={{ marginBottom: 0 }}
                    />
                  )}
                />
              </Box>
            </div>
          </div>
          <ControlledTextField
            labelTitle="Пожалуста, прикрепите ссылку на политику обработки персональных данных вашей компании (если такая имеется)"
            fieldName="company_confidential_url"
            control={control}
            error={!!errors.company_confidential_url?.message}
            helperText={errors.company_confidential_url?.message}
            placeholder="Вставьте ссылку"
            maxWidth={476}
          />
        </Box>
      </Box>
      <div className={classes.buttonContainer}>
        <Button type="button" className={classes.previousButton} onClick={handlePreviousClick}>
          Назад
        </Button>
        <Button className={classes.submitButton} type="submit">
          Далее
        </Button>
      </div>
    </form>
  )
}

export default React.memo(TabPF2)
