import React, { useEffect } from 'react'
import { Box, Button, Typography } from '@mui/material'
import { Controller } from 'react-hook-form'
import useStyles from './PartnersForm.styles'
import { ControlledSelectField, ControlledTextField } from '../../../Sub/FormElements'
import ControlledPartnersCompanyField from '../../../Sub/FormElements/controlled-partners-company-field/controlled-partners-company-field'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { setCurrentTab, setFormData } from '../../../../services/slices/partners-form-slice'

function TabPF1({ hookFormMethods }) {
  const dispatch = useAppDispatch()
  const classes = useStyles()
  const { control, handleSubmit, setValue, watch, getValues, errors } = hookFormMethods
  const { types, scopes, sizes, formData } = useAppSelector((state) => state.partnersForm)
  const company = watch('company_name')

  useEffect(() => {
    if (typeof company === 'object') {
      setValue('company_name', company?.name ?? '', { shouldValidate: true })
    }
  }, [company])

  const onSubmit = (data) => {
    dispatch(setFormData({ ...formData, ...data }))
    dispatch(setCurrentTab(2))
  }

  const handlePreviousClick = () => {
    const values = getValues()
    dispatch(setFormData({ ...formData, ...values }))
    dispatch(setCurrentTab(0))
  }

  return (
    <form className={classes.form} onSubmit={handleSubmit(onSubmit)}>
      <Box className={classes.scrolledArea}>
        <Typography variant="body3" sx={{ fontWeight: 400 }}>
          <Box>Спасибо!</Box>
          <Box mt="8px">
            Перед публикацией вакансии мы зададим несколько вопросов о компании. Общее время заполнение формы не более
            15 минут.
          </Box>
        </Typography>

        <Box mt="50px" sx={{ display: 'flex', flexDirection: 'column', gap: '14px', height: 'inherit' }}>
          <Box sx={{ maxWidth: 476 }}>
            <ControlledPartnersCompanyField
              control={control}
              error={!!errors?.company_name?.message}
              helperText={errors?.company_name?.message}
              isRequired
            />
          </Box>
          <ControlledTextField
            labelTitle="Прикрепите ссылку на сайт компании или проекта"
            fieldName="company_url"
            control={control}
            error={!!errors.company_url?.message}
            helperText={errors.company_url?.message}
            placeholder="Вставьте ссылку"
            maxWidth={476}
            isRequired
          />

          <Controller
            name="company_type"
            control={control}
            render={({ field: { name, value, onChange } }) => (
              <ControlledSelectField
                labelTitle="Тип компании"
                tipContent="Выберите из списка"
                name={name}
                value={value || []}
                onChange={onChange}
                error={!!errors?.company_type?.message}
                helperText={errors?.company_type?.message}
                items={types}
                displayEmpty
                maxWidth={476}
                isRequired
              />
            )}
          />
          <Controller
            name="company_scope"
            control={control}
            render={({ field: { name, value, onChange } }) => (
              <ControlledSelectField
                labelTitle="Индустрия (сфера работы)"
                tipContent="Выберите из списка"
                name={name}
                value={value || []}
                onChange={onChange}
                error={!!errors?.company_scope?.message}
                helperText={errors?.company_scope?.message}
                items={scopes}
                displayEmpty
                maxWidth={476}
                isRequired
              />
            )}
          />
          <Controller
            name="company_size"
            control={control}
            render={({ field: { name, value, onChange } }) => (
              <ControlledSelectField
                labelTitle="Размер компании"
                tipContent="Выберите из списка"
                name={name}
                value={value || []}
                onChange={onChange}
                error={!!errors?.company_size?.message}
                helperText={errors?.company_size?.message}
                items={sizes}
                displayEmpty
                maxWidth={476}
                isRequired
              />
            )}
          />
          <ControlledTextField
            labelTitle="Расскажите о вашей компании и продукте. Почему кандидаты хотят к вам откликаться?"
            fieldName="comment"
            control={control}
            error={!!errors.comment?.message}
            helperText={errors.comment?.message}
            placeholder="Введите ответ"
            multiline
            rows={3}
            maxWidth={476}
            textClass={classes.textAreaLarge}
          />
        </Box>
      </Box>
      <div className={classes.buttonContainer}>
        <Button type="button" className={classes.previousButton} onClick={handlePreviousClick}>
          Назад
        </Button>
        <Button className={classes.submitButton} type="submit">
          Далее
        </Button>
      </div>
    </form>
  )
}

export default React.memo(TabPF1)
