import React, { useEffect } from 'react'
import { ThemeProvider } from '@emotion/react'
import Dialog from '@mui/material/Dialog'
import DialogTitle from '@mui/material/DialogTitle'
import DialogContent from '@mui/material/DialogContent'
import useStyles from './PartnersForm.styles'
import Tabs from '../../../Sub/FormElements/vacancy-tabs/vacancy-tabs'
import Tab from '../../../Sub/FormElements/vacancy-tab/vacancy-tab'
import TabPanel from '../../../Sub/FormElements/tab-panel/tab-panel'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import useTabsForm from './useTabsForm'
import { getDirectories } from '../../../../services/axios/partners-form'
import theme from '../../../../utils/themeMUI5'
import TabPF5 from './TabPF5'
import TabPF4 from './TabPF4'
import TabPF3 from './TabPF3'
import TabPF2 from './TabPF2'
import TabPF1 from './TabPF1'
import TabPF0 from './TabPF0'
import TabPF6 from './TabPF6'

function PartnersForm() {
  const classes = useStyles()
  const dispatch = useAppDispatch()
  const { currentTab } = useAppSelector((state) => state.partnersForm)
  const { validationError, ...hookFormMethods } = useTabsForm(currentTab)

  useEffect(() => {
    dispatch(getDirectories())
  }, [])

  return (
    <ThemeProvider theme={theme}>
      <Dialog maxWidth="xl" open disableScrollLock className={classes.root} hideBackdrop>
        <DialogTitle className={classes.dialogTitle}>
          Публикация вакансии Яндекс Практикум
          {![0, 6].includes(currentTab) && (
            <Tabs value={currentTab} sx={{ marginLeft: '-23px', marginTop: '30px', marginBottom: '20px' }}>
              <Tab />
              <Tab label="1. Информация о компании" disabled />
              <Tab label="2. Вакансия" disabled />
              <Tab label="3. Условия" disabled />
              <Tab label="4. Требования" disabled />
              <Tab label="5. Брендирование" disabled />
            </Tabs>
          )}
        </DialogTitle>
        <DialogContent className={classes.dialogContent}>
          <TabPanel value={currentTab} index={0} style={{ height: '100%' }}>
            <TabPF0 hookFormMethods={hookFormMethods} />
          </TabPanel>
          <TabPanel value={currentTab} index={1} style={{ maxHeight: '100%' }}>
            <TabPF1 hookFormMethods={hookFormMethods} />
          </TabPanel>
          <TabPanel value={currentTab} index={2} style={{ maxHeight: '100%' }}>
            <TabPF2 hookFormMethods={hookFormMethods} />
          </TabPanel>
          <TabPanel value={currentTab} index={3} style={{ maxHeight: '100%' }}>
            <TabPF3 hookFormMethods={hookFormMethods} />
          </TabPanel>
          <TabPanel value={currentTab} index={4} style={{ maxHeight: '100%' }}>
            <TabPF4 hookFormMethods={hookFormMethods} />
          </TabPanel>
          <TabPanel value={currentTab} index={5} style={{ maxHeight: '100%', height: '100%' }}>
            <TabPF5 hookFormMethods={hookFormMethods} />
          </TabPanel>
          <TabPanel value={currentTab} index={6} style={{ maxHeight: '100%', height: '100%' }}>
            <TabPF6 hookFormMethods={hookFormMethods} />
          </TabPanel>
        </DialogContent>
      </Dialog>
    </ThemeProvider>
  )
}

export default React.memo(PartnersForm)
