/* eslint-disable no-continue */
/* eslint-disable no-restricted-syntax */
import React from 'react'
import { Box, Button, FormHelperText, Typography } from '@mui/material'
import { Link, useNavigate } from 'react-router-dom'
import { Controller } from 'react-hook-form'
import useStyles from './PartnersForm.styles'
import { ControlledTextField } from '../../../Sub/FormElements'
import { CustomRadioGroup } from '../../../Sub/ExportDialogs'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { resetPartnersForm, setCurrentTab, setFormData } from '../../../../services/slices/partners-form-slice'
import { createPartnerForm } from '../../../../services/axios/partners-form'

function TabPF6({ hookFormMethods }) {
  const dispatch = useAppDispatch()
  const { formData, isNetworkError, networkError, isLoading } = useAppSelector((state) => state.partnersForm)
  const classes = useStyles()
  const { control, handleSubmit, getValues, errors } = hookFormMethods
  const navigate = useNavigate()

  const onSubmit = async (tabData) => {
    await dispatch(setFormData({ ...formData, ...tabData }))
    const newData = { ...formData, ...tabData }
    const data = new FormData()
    for (const [key, value] of Object.entries(newData)) {
      if (key === 'cities') formData?.cities?.forEach((i) => data.append('cities', i?.id))
      else if (key === 'courses') formData?.courses?.forEach((i) => data.append('courses', i))
      else if (key === 'hr_profile' && value === -1) continue
      else if (value != null) {
        data.append(key, value)
      }
    }
    try {
      const response = await dispatch(createPartnerForm(data)).unwrap()
      if (response?.status === 'ok') {
        dispatch(resetPartnersForm())
        navigate('/partners-form-success')
      }
    } catch (e) {
      console.log({ e })
    }
  }

  const handlePreviousClick = () => {
    const values = getValues()
    dispatch(setFormData({ ...formData, ...values }))
    dispatch(setCurrentTab(5))
  }

  return (
    <form className={classes.form} onSubmit={handleSubmit(onSubmit)}>
      <Box className={classes.scrolledArea} mt="32px">
        <Typography variant="body3" sx={{ fontWeight: 400 }}>
          <Box>
            Спасибо за ответы на вопросы по вакансии! Мы проверим данные и опубликуем вакансию в сообществе Практикума.
          </Box>
          <Box mt="8px">
            Если не возражаете - мы зададим еще пару вопросов - не про вакансии, а про наше с вами сотрудничество :)
          </Box>
        </Typography>
        <Box mt="30px">
          <ControlledTextField
            labelTitle="Откуда вы про нас узнали? Откуда вы узнали, что в Практикуме можно нанимать ИТ и диджитал специалистов?"
            fieldName="source_info"
            control={control}
            error={!!errors.source_info?.message}
            helperText={errors.source_info?.message}
            placeholder="Введите ответ"
            multiline
            rows={3}
            maxWidth={476}
            textClass={classes.textAreaLarge}
            isRequired
          />
        </Box>
        <Box mt="22px" sx={{ maxWidth: '472px' }}>
          <Typography variant="body2" sx={{ fontWeight: 400, marginBottom: '5px' }}>
            Мы собираем истории успешного трудоустройства выпускников и публикуем их на сайте Карьерного центра
            Практикума. В случае успешного найма, интересно было бы вам участвовать в публикации?
            <span style={{ color: '#ff0200' }}> *</span>
          </Typography>
          <a
            href="https://practicum.yandex.ru/career-center/stories/"
            target="_blank"
            rel="noreferrer noopener"
            style={{
              textDecoration: 'none',
              color: '#1D6BF3',
              fontSize: '13px',
              fontWeight: 500,
            }}
          >
            https://practicum.yandex.ru/career-center/stories/
          </a>
          <Box>
            <Controller
              name="pub_agree"
              control={control}
              render={(field) => (
                <CustomRadioGroup
                  {...field}
                  options={[
                    { value: true, label: 'Да' },
                    { value: false, label: 'Нет' },
                  ]}
                />
              )}
            />
            <FormHelperText error className={classes.formHelper} sx={{ marginTop: '-5px' }}>
              {errors.pub_agree?.message || ' '}
            </FormHelperText>
          </Box>
        </Box>
      </Box>

      <Box>
        {networkError && (
          <FormHelperText error className={classes.formHelper} sx={{ marginTop: '-5px', textAlign: 'center' }}>
            Ошибка отправки формы
          </FormHelperText>
        )}
        <div className={classes.buttonContainer}>
          <Button type="button" className={classes.previousButton} onClick={handlePreviousClick}>
            Назад
          </Button>
          <Button className={classes.submitButton} type="submit" disabled={isLoading}>
            Отправить
          </Button>
        </div>
      </Box>
    </form>
  )
}

export default React.memo(TabPF6)
