import * as yup from 'yup'
import { urlOption, urlOptionRequired, workVacancyOption, multipleValuesOptions } from '../schemas'

const FILE_SIZE = 2097152

class PartnersFormSchema {
  static readonly zeroTab = yup.object().shape({
    hr_profile: yup.string().required('Выберите менеджера'),
  })

  static readonly firstTab = yup.object().shape({
    company_name: yup.string().required('Укажите название компании'),
    company_url: urlOptionRequired,
    company_type: yup.number().required('Выберите тип компании'),
    company_scope: yup.number().required('Выберите сферу деятельности'),
    company_size: yup.number().required('Выберите размер компании'),
  })

  static readonly secondTab = yup.object().shape({
    courses: yup.array().min(1, 'Укажите хотя бы одно направление').required('Укажите хотя бы одно направление'),
    position: workVacancyOption,
    candidate: yup.string().required('Выберите из списка'),
    salary: yup
      .number()
      .max(1000000000, 'Вряд ли это число может быть больше 1000000000')
      .moreThan(-1, 'Зарплата не может быть меньше нуля')
      .transform((value, original) => (original == null || original === '' ? null : value))
      .nullable(true)
      .typeError('Можно использовать только цифры 0-9')
      .when('salary_max', {
        is: (val: any) => !val,
        then: (theme) => theme.required('Заполни хотя бы одно поле зарплаты'),
        otherwise: (theme) => theme.max(yup.ref('salary_max'), 'Должно быть меньше значения "до"'),
      })
      .when('isSalaryNotRequired', {
        is: true,
        then: (theme) => theme.notRequired(),
      }),
    salary_max: yup
      .number()
      .max(1000000000, 'Вряд ли это число может быть больше 1000000000')
      .moreThan(-1, 'Зарплата не может быть меньше нуля')
      .transform((value, original) => (original == null || original === '' ? null : value))
      .nullable(true)
      .typeError('Можно использовать только цифры 0-9'),
    company_confidential_url: urlOption,
  })

  static readonly thirdTab = yup.object().shape({
    work_format: yup.number().required('Выберите формат работы'),
    cities: multipleValuesOptions,
    registration_in_the_state: yup.boolean().required('Выберите значение'),
    employment: yup.number().required('Выберите значение'),
    none_experience: yup.boolean().required('Выберите значение'),
    none_education: yup.boolean().required('Выберите значение'),
    test_task_url: urlOption,
  })

  static readonly fourthTab = yup.object().shape({
    conditions_and_tasks: yup.string().required('Введите ответ'),
    mandatory_requirements: yup.string().required('Введите ответ'),
    selection_stages: yup.string().required('Введите ответ'),
    feedback_giving: yup.string().required('Введите ответ'),
    vacancy_url: urlOption,
  })

  static readonly fifthTab = yup.object().shape({
    company_logo: yup
      .mixed()
      .test(
        'fileSize',
        `Максимальный размер файла ${FILE_SIZE / 1048576} MB`,
        (value) => !value || typeof value === 'string' || (value && value.size <= FILE_SIZE),
      ),
    big_cover: yup
      .mixed()
      .test(
        'fileSize',
        `Максимальный размер файла ${FILE_SIZE / 1048576} MB`,
        (value) => !value || typeof value === 'string' || (value && value.size <= FILE_SIZE),
      ),
    small_cover: yup
      .mixed()
      .test(
        'fileSize',
        `Максимальный размер файла ${FILE_SIZE / 1048576} MB`,
        (value) => !value || typeof value === 'string' || (value && value.size <= FILE_SIZE),
      ),
  })

  static readonly sixthTab = yup.object().shape({
    source_info: yup.string().required('Введите ответ'),
    pub_agree: yup.boolean().required('Выберите значение'),
  })
}

export default PartnersFormSchema
