import React from 'react'
import { Box, Button, FormHelperText } from '@mui/material'
import { Controller } from 'react-hook-form'
import useStyles from './PartnersForm.styles'
import { ControlledSelectField, ControlledTextField } from '../../../Sub/FormElements'
import ControlledMultiCityField from '../../../Sub/FormElements/controlled-multi-city-field/controlled-multi-city-field'
import { CustomRadioGroup } from '../../../Sub/ExportDialogs'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { setCurrentTab, setFormData } from '../../../../services/slices/partners-form-slice'

function TabPF3({ hookFormMethods }) {
  const dispatch = useAppDispatch()
  const { formData, employments, workFormats } = useAppSelector((state) => state.partnersForm)
  const classes = useStyles()
  const { control, handleSubmit, setValue, watch, trigger, errors, getValues } = hookFormMethods

  const onSubmit = (data) => {
    dispatch(setFormData({ ...formData, ...data }))
    dispatch(setCurrentTab(4))
  }

  const handlePreviousClick = () => {
    const values = getValues()
    dispatch(setFormData({ ...formData, ...values }))
    dispatch(setCurrentTab(2))
  }

  return (
    <form className={classes.form} onSubmit={handleSubmit(onSubmit)}>
      <Box className={classes.scrolledArea}>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: '14px', height: 'inherit' }}>
          <Controller
            name="work_format"
            control={control}
            render={({ field: { name, value, onChange } }) => (
              <ControlledSelectField
                labelTitle="В каком формате будет работать кандидат?"
                tipContent="Выберите из списка"
                name={name}
                value={value || []}
                onChange={onChange}
                error={!!errors?.work_format?.message}
                helperText={errors?.work_format?.message}
                items={workFormats}
                displayEmpty
                maxWidth={476}
                isRequired
              />
            )}
          />
          <ControlledMultiCityField
            control={control}
            error={!!errors?.cities?.message}
            helperText={errors?.cities?.message}
            width={476}
            limitTags={1}
            labelTitle="Укажите город, где находится офис или зарегистрировано юридическое лицо"
            isRequired
            hideLabel
          />
          <Box>
            <Controller
              name="registration_in_the_state"
              control={control}
              render={(field) => (
                <CustomRadioGroup
                  {...field}
                  options={[
                    { value: true, label: 'В штат' },
                    { value: false, label: 'Вне штата' },
                  ]}
                  labelTitle="Оформление в штат или вне штата?"
                  isRequired
                />
              )}
            />
            <FormHelperText error className={classes.formHelper} sx={{ marginTop: '-5px' }}>
              {errors.registration_in_the_state?.message || ' '}
            </FormHelperText>
          </Box>

          <Controller
            name="employment"
            control={control}
            render={({ field: { name, value, onChange } }) => (
              <ControlledSelectField
                labelTitle="Укажите формат занятости позиции"
                tipContent="Выберите из списка"
                name={name}
                value={value || []}
                onChange={onChange}
                error={!!errors?.employment?.message}
                helperText={errors?.employment?.message}
                items={employments}
                displayEmpty
                maxWidth={476}
                isRequired
              />
            )}
          />
          <Box>
            <Controller
              name="none_experience"
              control={control}
              render={(field) => (
                <CustomRadioGroup
                  {...field}
                  options={[
                    { value: true, label: 'Да' },
                    { value: false, label: 'Нет' },
                  ]}
                  labelTitle="Вы готовы рассматривать джунов без коммерческого опыта на эту вакансию?"
                  isRequired
                />
              )}
            />
            <FormHelperText error className={classes.formHelper} sx={{ marginTop: '-5px' }}>
              {errors.none_experience?.message || ' '}
            </FormHelperText>
          </Box>
          <ControlledTextField
            labelTitle='Если в предыдущем вопросе вы отметили "нет" - какой опыт обязательно нужен кандидату?'
            fieldName="none_experience_comment"
            control={control}
            error={!!errors.none_experience_comment?.message}
            helperText={errors.none_experience_comment?.message}
            placeholder="Введите ответ"
            multiline
            rows={3}
            maxWidth={476}
            textClass={classes.textAreaLarge}
          />
          <Box>
            <Controller
              name="none_education"
              control={control}
              render={(field) => (
                <CustomRadioGroup
                  {...field}
                  options={[
                    { value: true, label: 'Да' },
                    { value: false, label: 'Нет' },
                  ]}
                  labelTitle="Вы готовы рассматривать джунов без высшего профильного образования на эту вакансию?"
                  isRequired
                />
              )}
            />
            <FormHelperText error className={classes.formHelper} sx={{ marginTop: '-5px' }}>
              {errors.none_education?.message || ' '}
            </FormHelperText>
          </Box>
          <ControlledTextField
            labelTitle='Если в предыдущем вопросе вы отметили "нет" - какое образование обязательно нужно кандидату?'
            fieldName="none_education_comment"
            control={control}
            error={!!errors.none_education_comment?.message}
            helperText={errors.none_education_comment?.message}
            placeholder="Введите ответ"
            multiline
            rows={3}
            maxWidth={476}
            textClass={classes.textAreaLarge}
          />
          <ControlledTextField
            labelTitle="Если на первом этапе вы планируете отбирать кандидатов через тестовое задание - пожалуйста, обязательно прикрепите ссылку на задание"
            fieldName="test_task_url"
            control={control}
            error={!!errors.test_task_url?.message}
            helperText={errors.test_task_url?.message}
            placeholder="Вставьте ссылку"
            maxWidth={476}
          />
        </Box>
      </Box>
      {/* <Button className={classes.submitButton} type="submit" style={{ width: 306 }} disabled={!areTermsAgreed}>
        Далее
      </Button> */}
      <div className={classes.buttonContainer}>
        <Button type="button" className={classes.previousButton} onClick={handlePreviousClick}>
          Назад
        </Button>

        <Button className={classes.submitButton} type="submit">
          Далее
        </Button>
      </div>
    </form>
  )
}

export default React.memo(TabPF3)
