import React, { useMemo } from 'react'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import PartnersFormSchema from '../../../../utils/validation-schemas/partners-form-schemas'

const useTabsForm = (tab) => {
  const getResolver = (currentTab) => {
    switch (currentTab) {
      case 0:
        return yupResolver(PartnersFormSchema.zeroTab)
      case 1:
        return yupResolver(PartnersFormSchema.firstTab)
      case 2:
        return yupResolver(PartnersFormSchema.secondTab)
      case 3:
        return yupResolver(PartnersFormSchema.thirdTab)
      case 4:
        return yupResolver(PartnersFormSchema.fourthTab)
      case 5:
        return yupResolver(PartnersFormSchema.fifthTab)
      case 6:
        return yupResolver(PartnersFormSchema.sixthTab)
      default:
        return undefined
    }
  }
  const getFieldData = () => ({})

  const {
    control,
    handleSubmit,
    setValue,
    watch,
    register,
    trigger,
    getValues,
    setError,
    reset,
    formState: { errors },
  } = useForm({
    defaultValues: getFieldData(),
    mode: 'onChange',
    resolver: useMemo(() => getResolver(tab), [tab]),
  })

  const checkValidationError = () => {
    // Ошибки с бэка прилетят только на 5 табе, поэтому все проверки от него
    if (
      tab === 4 &&
      (errors?.company_name?.message ||
        errors?.website?.message ||
        errors?.position?.message ||
        errors?.courses?.message ||
        errors?.salary?.message ||
        errors?.salary_max?.message ||
        errors?.currency?.message ||
        errors?.company_confidential_url?.message)
    )
      return 'Допущена ошибка в блоке Общая информация'
    if (
      tab === 4 &&
      (errors?.cities?.message ||
        errors?.employment?.message ||
        errors?.design_formats?.message ||
        errors?.work_format?.message ||
        errors?.experience_type?.message ||
        errors?.education_type?.message ||
        errors?.test_task_url?.message)
    )
      return 'Допущена ошибка в блоке Теги'
    if (tab === 4 && (errors?.vacancy_info?.message || errors?.responsibilities?.message || errors?.circs?.message))
      return 'Допущена ошибка в блоке Вакансия'
    if (
      tab === 4 &&
      (errors?.mandatory_requirements?.message ||
        errors?.optional_requirements?.message ||
        errors?.design_formats?.message ||
        errors?.selection_stages?.message ||
        errors?.advice?.message ||
        errors?.chat_id?.message)
    )
      return 'Допущена ошибка в блоке Требования и ссылка'
    // Дропзона не рендерит серверную ошибку
    if (tab === 4 && errors?.big_cover?.message && errors?.small_cover?.message) {
      return `Ошибка в большой обложке: ${errors?.big_cover?.message}. Ошибка в маленькой обложке: ${errors?.small_cover?.message}`
    }
    if (tab === 4 && errors?.big_cover?.message) {
      return `Ошибка в большой обложке: ${errors?.big_cover?.message}`
    }
    if (tab === 4 && errors?.small_cover?.message) {
      return `Ошибка в большой обложке: ${errors?.small_cover?.message}`
    }
    // if (tab === 5 && (errors?.company_logo?.message || errors?.small_cover?.message || errors?.big_cover?.message))
    //   return 'Допущена ошибка в блоке Брендирование'
    return errors?.non_field_errors?.message
  }

  const validationError = checkValidationError()

  return {
    control,
    handleSubmit,
    setValue,
    watch,
    register,
    trigger,
    getValues,
    errors,
    setError,
    reset,
    validationError,
  }
}

export default useTabsForm
