import React, { useEffect, useState } from 'react'
import { Box, Button, FormHelperText, Typography } from '@mui/material'
import useStyles from './PartnersForm.styles'
import ControlledLogoField from '../../../Sub/FormElements/controlled-logo-field/controlled-logo-field'
import Dropzone from '../../../Sub/Dropzone/Dropzone'
import { COVERS_EXAMPLE_LINK, SUPPORTED_FORMATS } from '../../../../utils/constants'
import theme from '../../../../utils/themeMUI5'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { setCurrentTab, setFormData } from '../../../../services/slices/partners-form-slice'

function TabPF5({ hookFormMethods }) {
  const dispatch = useAppDispatch()
  const { formData } = useAppSelector((state) => state.partnersForm)
  const classes = useStyles()
  const { handleSubmit, setValue, watch, getValues, errors } = hookFormMethods
  const [logo, setLogo] = useState(null)
  const [bigCover, setBigCover] = useState([])
  const [cover, setCover] = useState([])
  const companyLogo = watch('company_logo')

  useEffect(() => {
    setValue('small_cover', Array.isArray(cover) ? cover[0] : cover)
  }, [cover])

  useEffect(() => {
    setValue('big_cover', Array.isArray(bigCover) ? bigCover[0] : bigCover)
  }, [bigCover])

  useEffect(() => {
    const newLogo = companyLogo instanceof File ? URL.createObjectURL(companyLogo) : companyLogo
    setLogo(newLogo)
  }, [companyLogo])

  const onSubmit = (data) => {
    dispatch(setFormData({ ...formData, ...data }))
    dispatch(setCurrentTab(6))
  }

  const handlePreviousClick = () => {
    const values = getValues()
    dispatch(setFormData({ ...formData, ...values }))
    dispatch(setCurrentTab(4))
  }

  const handleImageChange = (e) => {
    if (e.target.files) {
      setValue('company_logo', e.target.files[0], { shouldValidate: true, shouldDirty: false })
    } else if (e.target?.name === 'image' && e.target?.value === null) {
      setValue('company_logo', null, { shouldValidate: true, shouldDirty: false })
    }
  }

  return (
    <form className={classes.form} onSubmit={handleSubmit(onSubmit)}>
      <ControlledLogoField
        avatar={logo}
        helperText={errors?.company_logo?.message}
        onChange={handleImageChange}
        isPartnersForm
      />
      <Box
        mt="-11px"
        mb="12px"
        sx={{ display: 'flex', gap: '24px', width: '100%', minHeight: '176px', boxSizing: 'border-box' }}
      >
        <Box sx={{ display: 'flex', flexDirection: 'column', boxSizing: 'border-box' }}>
          <Dropzone
            filesState={[bigCover, setBigCover]}
            maxFilesNumber={1}
            label="Загрузить большую обложку"
            nameTip=""
            fileTip="Перетащи файлы: .png, .jpg, не более 2 Мб. Рекомендуемое разрешение: 1280 Х 1024"
            error={errors?.big_cover?.message}
            isBigCover
            acceptedFormats={SUPPORTED_FORMATS}
            isVacancyPopup
          />
        </Box>
        <Box sx={{ display: 'flex', flexDirection: 'column', boxSizing: 'border-box' }}>
          <Dropzone
            filesState={[cover, setCover]}
            maxFilesNumber={1}
            label="Загрузить маленькую обложку"
            nameTip=""
            fileTip="Перетащи файлы: .png, .jpg, не более 2 Мб. Рекомендуемое разрешение: 320 Х 400"
            error={errors?.small_cover?.message}
            isCover
            acceptedFormats={SUPPORTED_FORMATS}
            isVacancyPopup
          />
        </Box>
      </Box>
      <Typography mt="-52px" sx={{ alignSelf: 'flex-start' }} variant="body" color={theme.color.black500}>
        Посмотреть{' '}
        <a
          style={{ color: theme.color.blueMain, textDecoration: 'none' }}
          href={COVERS_EXAMPLE_LINK}
          target="_blank"
          rel="noreferrer"
        >
          примеры обложек
        </a>
      </Typography>

      <div className={classes.buttonContainerWithError}>
        <FormHelperText error className={classes.formHelper} sx={{ marginTop: '-5px', textAlign: 'center' }}>
          {errors?.big_cover?.message ? `Большая обложка: ${errors?.big_cover?.message}` : ' '}
        </FormHelperText>

        <FormHelperText error className={classes.formHelper} sx={{ marginTop: '-5px', textAlign: 'center' }}>
          {errors?.small_cover?.message ? `Маленькая обложка: ${errors?.small_cover?.message}` : ' '}
        </FormHelperText>

        <Box sx={{ display: 'flex', gap: '20px', justifyContent: 'center' }}>
          <Button type="button" className={classes.previousButton} onClick={handlePreviousClick}>
            Назад
          </Button>
          <Button className={classes.submitButton} type="submit">
            Далее
          </Button>
        </Box>
      </div>
    </form>
  )
}

export default React.memo(TabPF5)
