import React from 'react'
import { Box, Button, Typography } from '@mui/material'
import { Controller } from 'react-hook-form'
import useStyles from './PartnersForm.styles'
import CheckboxItem from '../../../Sub/CheckboxGroup/CheckboxItem/CheckboxItem'
import { ControlledSelectField } from '../../../Sub/FormElements'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { setCurrentTab, setFormData } from '../../../../services/slices/partners-form-slice'

function TabPF0({ hookFormMethods }) {
  const dispatch = useAppDispatch()
  const classes = useStyles()
  const { control, handleSubmit, watch, errors } = hookFormMethods
  const areTermsAgreed = watch('agree_terms')
  const { managers, formData } = useAppSelector((state) => state.partnersForm)

  const onSubmit = (data) => {
    dispatch(setFormData({ ...formData, ...data }))
    dispatch(setCurrentTab(1))
  }

  return (
    <form className={classes.form} onSubmit={handleSubmit(onSubmit)}>
      <Box className={classes.scrolledArea} mt="32px">
        <Typography variant="body3" sx={{ fontWeight: 400 }}>
          <Box>Привет!</Box>
          <Box mt="8px">
            Спасибо за обращение в Карьерный Центр Яндекс Практикума! Мы надеемся, что скоро сможем показать вам
            подходящих кандидатов :)
          </Box>
        </Typography>
        <Typography variant="body3">
          <Box mt="32px">Перед стартом мы хотим уточнить формат, в котором будет проходить сотрудничество.</Box>
        </Typography>

        <Typography variant="body3" sx={{ fontWeight: 400 }}>
          <Box mt="8px">После заполнения этой формы:</Box>
          <ul style={{ padding: '8px 23px', margin: 0 }}>
            <li>мы придем с вопросами, если они возникнут;</li>
            <li>мы опубликуем вакансию на закрытой карьерной платформе Практикума;</li>
            <li>наш телеграм бот будет передавать вам все поступающие отклики по будним дням в 10:00.</li>
          </ul>
          <Box mt="17px">
            Далее, настанет время скрининга и собеседований. Для Практикума важно, чтобы все наши кандидаты получили
            ответ и обратную связь от работодателя, и поэтому:
          </Box>
          <ul style={{ padding: '8px 23px', margin: 0 }}>
            <li>мы будем приходить за обновлением статусов кандидатов 1 раз в неделю;</li>
            <li>кандидатам, получившим отказ по итогам скрининга, мы передадим ваше решение и обратную связь;</li>
            <li>
              тем кандидатам, кто прошёл интервью, выполнил тестовое задание или общался с вами, понадобится обратная
              связь от вас. Мы просим связаться с ними и сообщить решение.
            </li>
          </ul>
        </Typography>
        <Box mt="30px">
          <Controller
            name="hr_profile"
            control={control}
            render={({ field: { name, value, onChange } }) => (
              <ControlledSelectField
                labelTitle="Укажите менеджера, с которым вы общались"
                tipContent="Выберите из списка"
                name={name}
                value={value || []}
                onChange={onChange}
                error={!!errors?.hr_profile?.message}
                helperText={errors?.hr_profile?.message}
                items={[...managers, { id: -1, name: 'Не помню' }]}
                displayEmpty
                maxWidth={476}
                isRequired
              />
            )}
          />
        </Box>
        <Box mt="22px">
          <Controller
            name="agree_terms"
            control={control}
            render={({ field }) => (
              <CheckboxItem
                {...field}
                checkboxLabel="Я принимаю условия сотрудничества с карьерным центром Яндекс Практикум"
                checked={areTermsAgreed}
                margin={{ marginBottom: 10 }}
              />
            )}
          />
        </Box>
      </Box>
      <Button className={classes.submitButton} type="submit" style={{ width: 306 }} disabled={!areTermsAgreed}>
        Далее
      </Button>
    </form>
  )
}

export default React.memo(TabPF0)
